import React from 'react'
import Link from 'gatsby-link'

const IndexPage = ({data}) => (
    <div className="container dynamic-grid">
      {data.allContentfulProduct.edges.map(post=>(
        console.log(post.node.image[0].file.url),
        <article key={post.node.id} className="grid-item content-box">
          <div className="inner">
            <img className="content-box-thumb" src={post.node.image[0].file.url} alt="tech image" />
            <h1 className="content-box-header">
            {post.node.image[0].title}
          </h1>
            <p className="content-box-blurb">
              {post.node.image[0].description}
            </p>
          </div>
        </article>
      ))}
    </div>
)

export default IndexPage


export const indexQuery = graphql`
  query pagequery {
    allContentfulProduct {
      edges {
        node {
          id
          image {
            id
            description
            title
            file {
              url
              fileName
              contentType
            }
          }
        }
      }
    }
  }
  
`


